<template>
  <div class="footer-content">
    <div class="flex">
      <span class="page-count">{{ first }}-{{ last }} of {{ tableLength }} items</span>
      <el-pagination
        layout="sizes, prev, pager, next, jumper"
        :current-page.sync="currentPage"
        :page-sizes="pageSizes"
        :page-size="pageSize"
        :total="tableLength"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>

export default {
  name: 'PaginationFooter',
  props: {
    tableLength: {
      type: Number,
      required: true
    },
    page: {
      type: Object,
      required: true
    },
    showDefaultTable: {
      type: Boolean,
      required: true
    },
    pageSizes: {
      type: Array,
      default: () => [20, 50, 100]
    }
  },
  mounted () {
    this.getOffSet()
  },
  data () {
    return {
      pageSize: this.page.limit,
      currentPage: (this.page.limit + this.page.offset) / this.page.limit,
      first: 1,
      last: 20,
      limit: this.page.callLimit > this.tableLength ? this.page.callLimit : this.tableLength
    }
  },
  watch: {
    tableLength: function (length) {
      this.handleCurrentChange(1)
    }
  },
  methods: {
    handleSizeChange (val) {
      this.pageSize = val
      this.getOffSet()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getOffSet()
    },
    getOffSet () {
      const offset = this.pageSize * (this.currentPage - 1)
      const pageCalc = this.pageSize * this.currentPage
      let page = {
        limit: this.pageSize,
        currentPage: this.currentPage,
        offset: offset,
        callNext: (this.currentPage + 1) > this.tableLength
      }

      if (!this.showDefaultTable) {
        page.callNext = (offset + 1) > this.limit
        if (page.callNext) {
          this.limit += this.page.callLimit
        }
      }

      this.first = offset + 1
      this.last = pageCalc < this.tableLength ? pageCalc : this.tableLength
      this.$emit('page-change', page)
    }
  }
}
</script>

<style>
.footer-content {
  width: 100%;
  display: flex;
  padding: 14px 0px;
  justify-content: center;
  background-color: #fafafa;
}

.page-count {
  margin: 5px;
  font-size: 18px;
}

.footer-content > div > .el-pagination > ul > li,
.el-pagination .btn-next,
.el-pagination .btn-prev {
  background-color: #fafafa !important;
  font-size: 18px !important;
}

.footer-content > div > .el-pagination .btn-next .el-icon,
.el-pagination .btn-prev .el-icon {
  font-size: 18px !important;
}
</style>
